export class YMapsObjects {
    /** ÐÐ°ÑÑÐ° */
    Map;
    /** ÐÐ°ÑÐºÐµÑÑ */
    markers;
    /** ÐÑÑÑ Ð´Ð¾ Ð¸Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ñ Ð±Ð°Ð»ÑÐ½Ð° */
    pathToBaloon;

    /**
     * @param {{Map: any, markers: Array, pathToBaloon: string}} params
     */
    constructor(params) {
        this.markers = params.markers;
        this.pathToBaloon = params.pathToBaloon
        this.Map = params.Map;
    }

    /** ÐÐ¾Ð»ÑÑÐ¸ÑÑ ÑÐ°Ð±Ð»Ð¾Ð½ Ð±Ð°Ð»ÑÐ½Ð° */
    fGetBalloonLayout() {
        const myBalloonLayout = ymaps.templateLayoutFactory.createClass(
            '<div class="ymap-pvz-popover">' +
            '<div class="ymap-pvz-popover-close">&times;</div>' +
            '<div class="ymap-pvz-popover-inner">' +
            '$[[options.contentLayout observeSize]]' +
            '</div>' +
            '<div class="ymap-pvz-popover-arrow"></div>' +
            '</div>', {
            /**
             * Ð¡ÑÑÐ¾Ð¸Ñ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ Ð¼Ð°ÐºÐµÑÐ° Ð½Ð° Ð¾ÑÐ½Ð¾Ð²Ðµ ÑÐ°Ð±Ð»Ð¾Ð½Ð° Ð¸ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑ ÐµÐ³Ð¾ Ð² ÑÐ¾Ð´Ð¸ÑÐµÐ»ÑÑÐºÐ¸Ð¹ HTML-ÑÐ»ÐµÐ¼ÐµÐ½Ñ.
             * @see https://api.yandex.ru/maps/doc/jsapi/2.1/ref/reference/layout.templateBased.Base.xml#build
             * @function
             * @name build
             */
            build: function () {
                this.constructor.superclass.build.call(this);

                this._$element = this.getParentElement().querySelector('.ymap-pvz-popover')

                this.applyElementOffset();

                const elClose = this._$element.querySelector('.ymap-pvz-popover-close')
                elClose.addEventListener('click', this.onCloseClick.bind(this))
            },

            /**
             * Ð£Ð´Ð°Ð»ÑÐµÑ ÑÐ¾Ð´ÐµÑÐ¶Ð¸Ð¼Ð¾Ðµ Ð¼Ð°ÐºÐµÑÐ° Ð¸Ð· DOM.
             * @see https://api.yandex.ru/maps/doc/jsapi/2.1/ref/reference/layout.templateBased.Base.xml#clear
             * @function
             * @name clear
             */
            clear: function () {
                const elClose = this._$element.querySelector('.ymap-pvz-popover-close')
                elClose.removeEventListener('click', this.onCloseClick.bind(this))

                this.constructor.superclass.clear.call(this);
            },

            /**
             * ÐÐµÑÐ¾Ð´ Ð±ÑÐ´ÐµÑ Ð²ÑÐ·Ð²Ð°Ð½ ÑÐ¸ÑÑÐµÐ¼Ð¾Ð¹ ÑÐ°Ð±Ð»Ð¾Ð½Ð¾Ð² ÐÐÐ Ð¿ÑÐ¸ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ð¸ ÑÐ°Ð·Ð¼ÐµÑÐ¾Ð² Ð²Ð»Ð¾Ð¶ÐµÐ½Ð½Ð¾Ð³Ð¾ Ð¼Ð°ÐºÐµÑÐ°.
             * @see https://api.yandex.ru/maps/doc/jsapi/2.1/ref/reference/IBalloonLayout.xml#event-userclose
             * @function
             * @name onSublayoutSizeChange
             */
            onSublayoutSizeChange: function () {
                myBalloonLayout.superclass.onSublayoutSizeChange.apply(this, arguments);

                if(!this._isElement(this._$element)) {
                    return;
                }

                this.applyElementOffset();

                this.events.fire('shapechange');
            },

            /**
             * Ð¡Ð´Ð²Ð¸Ð³Ð°ÐµÐ¼ Ð±Ð°Ð»ÑÐ½, ÑÑÐ¾Ð±Ñ "ÑÐ²Ð¾ÑÑÐ¸Ðº" ÑÐºÐ°Ð·ÑÐ²Ð°Ð» Ð½Ð° ÑÐ¾ÑÐºÑ Ð¿ÑÐ¸Ð²ÑÐ·ÐºÐ¸.
             * @see https://api.yandex.ru/maps/doc/jsapi/2.1/ref/reference/IBalloonLayout.xml#event-userclose
             * @function
             * @name applyElementOffset
             */
            applyElementOffset: function () {
                const nLeft = -(this._$element.offsetWidth / 2)
                const nArrowHeight = 6
                const nTop = -(this._$element.offsetHeight + nArrowHeight + 52)
                this._$element.style.left = `${nLeft}px`
                this._$element.style.top = `${nTop}px`
            },

            /**
             * ÐÐ°ÐºÑÑÐ²Ð°ÐµÑ Ð±Ð°Ð»ÑÐ½ Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° ÐºÑÐµÑÑÐ¸Ðº, ÐºÐ¸Ð´Ð°Ñ ÑÐ¾Ð±ÑÑÐ¸Ðµ "userclose" Ð½Ð° Ð¼Ð°ÐºÐµÑÐµ.
             * @see https://api.yandex.ru/maps/doc/jsapi/2.1/ref/reference/IBalloonLayout.xml#event-userclose
             * @function
             * @name onCloseClick
             */
            onCloseClick: function (e) {
                e.preventDefault();
                this.events.fire('userclose');
            },

            /**
             * ÐÑÐ¿Ð¾Ð»ÑÐ·ÑÐµÑÑÑ Ð´Ð»Ñ Ð°Ð²ÑÐ¾Ð¿Ð¾Ð·Ð¸ÑÐ¸Ð¾Ð½Ð¸ÑÐ¾Ð²Ð°Ð½Ð¸Ñ (balloonAutoPan).
             * @see https://api.yandex.ru/maps/doc/jsapi/2.1/ref/reference/ILayout.xml#getClientBounds
             * @function
             * @name getClientBounds
             * @returns {Number[][]} ÐÐ¾Ð¾ÑÐ´Ð¸Ð½Ð°ÑÑ Ð»ÐµÐ²Ð¾Ð³Ð¾ Ð²ÐµÑÑÐ½ÐµÐ³Ð¾ Ð¸ Ð¿ÑÐ°Ð²Ð¾Ð³Ð¾ Ð½Ð¸Ð¶Ð½ÐµÐ³Ð¾ ÑÐ³Ð»Ð¾Ð² ÑÐ°Ð±Ð»Ð¾Ð½Ð° Ð¾ÑÐ½Ð¾ÑÐ¸ÑÐµÐ»ÑÐ½Ð¾ ÑÐ¾ÑÐºÐ¸ Ð¿ÑÐ¸Ð²ÑÐ·ÐºÐ¸.
             */
            getShape: function () {
                if(!this._isElement(this._$element)) {
                    return myBalloonLayout.superclass.getShape.call(this);
                }

                const nTop = Number(this._$element.style.top.substring(0, this._$element.style.top.length - 2))
                const nLeft = Number(this._$element.style.left.substring(0, this._$element.style.left.length - 2))

                return new ymaps.shape.Rectangle(new ymaps.geometry.pixel.Rectangle([
                    [nLeft, nTop], [
                        nLeft + this._$element.offsetWidth,
                        nTop + this._$element.offsetHeight + this._$element.querySelector('.ymap-pvz-popover-arrow').offsetHeight
                    ]
                ]));
            },

            /**
             * ÐÑÐ¾Ð²ÐµÑÑÐµÐ¼ Ð½Ð°Ð»Ð¸ÑÐ¸Ðµ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ° (Ð² ÐÐ Ð¸ ÐÐ¿ÐµÑÐµ ÐµÐ³Ð¾ ÐµÑÐµ Ð¼Ð¾Ð¶ÐµÑ Ð½Ðµ Ð±ÑÑÑ).
             * @function
             * @private
             * @name _isElement
             * @param {HTMLDivElement} [element] Ð­Ð»ÐµÐ¼ÐµÐ½Ñ.
             * @returns {Boolean} Ð¤Ð»Ð°Ð³ Ð½Ð°Ð»Ð¸ÑÐ¸Ñ.
             */
            _isElement: function (element) {
                return element && element.querySelector('.ymap-pvz-popover-arrow');
            }
        })
        return myBalloonLayout
    }

    /** Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ Ð¾Ð±ÑÐµÐºÑ ÐºÐ¾Ð½ÑÑÐ¾Ð»Ð°, Ñ Ð¿Ð¾Ð¼Ð¾ÑÑÑ templateLayoutFactory */
    fCreate() {
        this.Map.geoObjects.removeAll();
        
        const objectManagerConfig = {
            // Ð§ÑÐ¾Ð±Ñ Ð¼ÐµÑÐºÐ¸ Ð½Ð°ÑÐ°Ð»Ð¸ ÐºÐ»Ð°ÑÑÐµÑÐ¸Ð·Ð¾Ð²Ð°ÑÑÑÑ, Ð²ÑÑÑÐ°Ð²Ð»ÑÐµÐ¼ Ð¾Ð¿ÑÐ¸Ñ.
            clusterize: true,
            // ÐÐ¿ÑÐ¸Ð¸ Ð´Ð»Ñ ÐºÐ°ÑÑÐ¾Ð¼Ð½Ð¾Ð¹ Ð¸ÐºÐ¾Ð½ÐºÐ¸ Ð¾Ð´Ð¸Ð½Ð¾ÑÐ½Ð¾Ð¹ Ð¼ÐµÑÐºÐ¸
            geoObjectIconLayout: 'default#image',
            // Ð Ð°Ð·Ð¼ÐµÑÑ Ð¼ÐµÑÐºÐ¸.
            geoObjectIconImageSize: [50, 50], 
            // Ð¡Ð¼ÐµÑÐµÐ½Ð¸Ðµ Ð»ÐµÐ²Ð¾Ð³Ð¾ Ð²ÐµÑÑÐ½ÐµÐ³Ð¾ ÑÐ³Ð»Ð° Ð¸ÐºÐ¾Ð½ÐºÐ¸ Ð¾ÑÐ½Ð¾ÑÐ¸ÑÐµÐ»ÑÐ½Ð¾ ÐµÑ "Ð½Ð¾Ð¶ÐºÐ¸" (ÑÐ¾ÑÐºÐ¸ Ð¿ÑÐ¸Ð²ÑÐ·ÐºÐ¸).
            geoObjectIconImageOffset: [-25, -50],
        }
        if (this.pathToBaloon) {
            // Ð¡Ð²Ð¾Ñ Ð¸Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ Ð¸ÐºÐ¾Ð½ÐºÐ¸ Ð¼ÐµÑÐºÐ¸.
            objectManagerConfig.clusterIconImageHref = this.pathToBaloon
            // ÐÐ¿ÑÐ¸Ð¸ Ð´Ð»Ñ ÐºÐ°ÑÑÐ¾Ð¼Ð½Ð¾Ð¹ Ð¸ÐºÐ¾Ð½ÐºÐ¸ ÐºÐ»Ð°ÑÑÐµÑÐ°
            objectManagerConfig.clusterIconLayout = 'default#image'
            // Ð Ð°Ð·Ð¼ÐµÑÑ Ð¼ÐµÑÐºÐ¸.
            objectManagerConfig.clusterIconImageSize = [70, 70]
            // Ð¡Ð¼ÐµÑÐµÐ½Ð¸Ðµ Ð»ÐµÐ²Ð¾Ð³Ð¾ Ð²ÐµÑÑÐ½ÐµÐ³Ð¾ ÑÐ³Ð»Ð° Ð¸ÐºÐ¾Ð½ÐºÐ¸ Ð¾ÑÐ½Ð¾ÑÐ¸ÑÐµÐ»ÑÐ½Ð¾
            // ÐµÑ "Ð½Ð¾Ð¶ÐºÐ¸" (ÑÐ¾ÑÐºÐ¸ Ð¿ÑÐ¸Ð²ÑÐ·ÐºÐ¸).
            objectManagerConfig.clusterIconImageOffset = [-35, -70]
        } else {
            objectManagerConfig.clusterIconLayout = 'default#pieChart'
            objectManagerConfig.clusterIconPieChartRadius = 25
            objectManagerConfig.clusterIconPieChartCoreRadius = 20
            objectManagerConfig.clusterIconPieChartDoughnut = false

        }
        const objectManager = new ymaps.ObjectManager(objectManagerConfig);

        const objectColection = [];

        for (let i = 0; i < this.markers.length; i++) {
            const vMarker = this.markers[i]
            const oneObject = {
                type: 'Feature',
                id: vMarker.id,
                geometry: {
                    type: 'Point',
                    coordinates: [vMarker.latitude, vMarker.longitude]
                },
            }
            if (vMarker.balloonContent) {
                oneObject.options = {
                    balloonLayout: this.fGetBalloonLayout(),
                    balloonContentLayout: ymaps.templateLayoutFactory.createClass(
                        vMarker.balloonContent.html, 
                        vMarker.balloonContent.methods,
                    ),
                    hideIconOnBalloonOpen: false,
                    balloonPanelMaxMapArea: 0,
                }
                oneObject.properties = {
                    balloonPanelMaxMapArea: 0,
                }
            }
            if (vMarker.iconImageHref) {
                if (!oneObject.options) {
                    oneObject.options = {}
                }
                oneObject.options.iconImageHref = vMarker.iconImageHref
                oneObject.options.iconColor = vMarker.iconColor
            }
            objectColection.push(oneObject)
        }
        objectManager.add(objectColection);
        this.Map.geoObjects.add(objectManager);
        return objectManager
    }
}
